import React, { useContext, useState } from 'react'
import './index.css'
import { Switch } from 'antd'
import { Slider } from '@douyinfe/semi-ui'
import { GlobalContext } from '../globalContext'

const Index = props => {
    const gctx = useContext(GlobalContext)
    const [genMode, setgenMode] = gctx.genMode
    const [tokenCount, settokenCount] = gctx.tokenCount
    const [temp, settemp] = gctx.temp
    const [topP, settopP] = gctx.topP
    const [PP, setPP] = gctx.PP
    const [CP, setCP] = gctx.CP
    return (
        <div className="cpnt_leftBar" style={props.style}>
            <div className="mode">
                <div className="label">Mode</div>
                <Switch checkedChildren="续写" unCheckedChildren="对话" checked={genMode} onChange={e => setgenMode(e)} />
            </div>
            <div className="block">
                <div className="row">
                    <div className="info">
                        <div className="label">Token Count</div>
                        <div className="value">{tokenCount}</div>
                    </div>
                    <Slider min={64} max={2048} step={64} value={tokenCount} onChange={e => settokenCount(e)} />
                </div>
                <div className="row">
                    <div className="info">
                        <div className="label">Temperature</div>
                        <div className="value">{temp}</div>
                    </div>
                    <Slider min={0} max={2} step={0.1} value={temp} onChange={e => settemp(e)} />
                </div>
                <div className="row">
                    <div className="info">
                        <div className="label">Top P</div>
                        <div className="value">{topP}</div>
                    </div>
                    <Slider min={0} max={1} step={0.1} value={topP} onChange={e => settopP(e)} />
                </div>
                <div className="row">
                    <div className="info">
                        <div className="label">Presence Penalty</div>
                        <div className="value">{PP}</div>
                    </div>
                    <Slider min={0} max={1} step={0.1} value={PP} onChange={e => setPP(e)} />
                </div>
                <div className="row">
                    <div className="info">
                        <div className="label">Count Penalty</div>
                        <div className="value">{CP}</div>
                    </div>
                    <Slider min={0} max={1} step={0.1} value={CP} onChange={e => setCP(e)} />
                </div>
            </div>
        </div>
    )
}

export default Index
