import React, { useEffect, useState, useContext } from 'react'
import './index.css'
import API from '../../utils/API.js'
import { GlobalContext } from '../globalContext'

let thinking = null

const Index = props => {
    const gctx = useContext(GlobalContext)
    const [genMode, setgenMode] = gctx.genMode
    const [tokenCount, settokenCount] = gctx.tokenCount
    const [temp, settemp] = gctx.temp
    const [topP, settopP] = gctx.topP
    const [PP, setPP] = gctx.PP
    const [CP, setCP] = gctx.CP
    const [generating, setgenerating] = useState(false)
    const [text, settext] = useState(`“斗之力，三段！”
    望着测验魔石碑上面闪亮得甚至有些刺眼的五个大字，少女面无表情，唇角有着一抹自嘲，紧握的手掌，因为大力而导致略微尖锐的指甲深深地刺进了掌心之中，带来一阵阵钻心的疼痛。`)
    const [snippet, setsnippet] = useState('')
    useEffect(() => {
        setsnippet('')
    }, [text])
    useEffect(() => {
        if (generating) {
            let anima = " I'm thinking"
            thinking = setInterval(() => {
                if (anima.length >= " I'm thinking".length + 3) {
                    anima = " I'm thinking"
                } else {
                    anima += '.'
                }
                setsnippet(anima)
            }, 300)
        } else {
            clearInterval(thinking)
        }
    }, [generating])
    return (
        <div className="cpnt_editArea" style={props.style}>
            <div className="container">
                <textarea className="input" value={text} onChange={e => settext(e.target.value)} onKeyDown={checkTab}></textarea>
                <span className="text">{text}</span>
                <span className="snippet">{snippet}</span>
            </div>
        </div>
    )
    function checkTab(e) {
        if (e.key === 'Tab') {
            e.preventDefault()
            if (!snippet) {
                gen()
            } else {
                settext(text + snippet)
                setsnippet('')
            }
        }
    }
    function gen() {
        if (generating) return
        setgenerating(true)
        try {
            if (genMode) {
                API.write(text.slice(-300), {
                    tokenCount,
                    temp,
                    topP,
                    PP,
                    CP
                }).then(res => {
                    setsnippet(res.data)
                    setgenerating(false)
                })
            } else {
                API.reset().then(res => {
                    API.chat(text.slice(-200)).then(res => {
                        setsnippet(res.data)
                        setgenerating(false)
                    })
                })
            }
        } catch (error) {
            console.log(error)
            setgenerating(false)
        }
    }
}

export default Index
