import React from 'react'
import './index.css'
import LeftBar from '../../components/leftBar'
import EditArea from '../../components/editArea'

const Index = () => {
    return (
        <div className="page_write" style={{ display: 'flex' }}>
            <LeftBar style={{ width: '300px' }}></LeftBar>
            <EditArea style={{ width: 'calc(100% - 300px)' }}></EditArea>
        </div>
    )
}

export default Index
