import axios from 'axios'

const baseURL = 'http://36.133.182.227:10009'

function request(method, url, data) {
    return axios({
        method,
        url: baseURL + url,
        data
    })
}

const API = {
    chat: msg => request('POST', '/gen', { msg }),
    reset: () => request('POST', '/gen', { msg: '+reset' }),
    write: (msg, config = {}) => {
        config['msg'] = '+gen ' + msg
        return request('POST', '/gen', config)
    },
    keep: () => request('POST', '/gen', { msg: '+++' }),
    setLen: len => request('POST', '/set', { len }),
    set: (name, value) => {
        let data = {}
        data[name] = value
        return request('POST', '/set', data)
    }
}

export default API
